import React, { Component } from 'react';
import PropTypes from 'prop-types';
import api from '../utils/api.js';
import Spinner from 'react-spinkit';

function ReposGrid(props) {
  return(
    <ul className="popular-list">
      {props.repos.map(function(repo, index) {
        return (
          <li 
            key={repo.name} 
            className='popular-item'>
            <div className="popular-rank">
              #{index + 1} 
            </div>
            <ul className="space-list-items">
              <li>
                <img src={repo.owner.avatar_url} alt={'Avatar for '+repo.owner.login} className="avatar"/>
              </li>
              <li>
                <a href={repo.html_url}>{repo.name}</a>
              </li>
              <li>@{repo.owner.login}</li>
              <li>{repo.stargazers_count} stars</li>
            </ul>
          </li>
        )
      })}
    </ul>
  );
}

ReposGrid.propTypes = {
  repos: PropTypes.array.isRequired,
}

function SelectLanguage(props) {
  var languages = ['All', 'JavaScript', 'Ruby', 'Java', 'Python', 'CSS'];
  return(
    <ul className="languages">
      {languages.map(function(lang) {
        return (
          <li 
            style = {lang === props.selectedLanguage ? {color:'#d0021b'} : null}
            onClick = {props.onSelect.bind(null,lang)}
            key={lang}>
            {lang}
          </li>
        )
      })}
    </ul>
  )
}

SelectLanguage.propTypes = {
  selectedLanguage: PropTypes.string.isRequired,
  onSelect: PropTypes.func.isRequired
}


class Popular extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedLanguage: 'All',
      repos: null
    };

    this.updateLanguage = this.updateLanguage.bind(this);
  }

  updateLanguage(lang) {
    this.setState(function() {
      return {
        selectedLanguage: lang,
        repos: null
      }
    });

    api.fetchPopularRepos(lang)
      .then(function(repos) {
        this.setState(function(){
          return {
            repos: repos
          }
        });
      }.bind(this));
  }

  componentDidMount() {
    this.updateLanguage(this.state.selectedLanguage);
  }

  render() {
    
    return(
      <div>
        <SelectLanguage
          selectedLanguage = {this.state.selectedLanguage}
          onSelect = {this.updateLanguage}
         />
         {!this.state.repos ? <Spinner name='line-scale' color="steelblue" className="spinner"/> : <ReposGrid repos = {this.state.repos} /> }
      </div>
    );      
  }
}

export default Popular;


